/**
 * Contains the table that displays the ECG event tags
 * and its related information
 */

// CSS
import "../style/Events.css"

// eslint-disable-next-line no-use-before-define
import React from "react"

// react-bootstrap
import Table from "react-bootstrap/Table"

// react-i18next
import { useTranslation } from "react-i18next"

const Events = (props) => {
  const { t } = useTranslation()

  return (
    <>
      <h2>{t("Events")}</h2>
      <div className="event-table">
      <Table striped bordered hover size="sm" >
        <thead>
          <tr>
            <th>{t("Time")}</th>
            <th>{t("Symbol")}</th>
            <th>{t("Text")}</th>
            <th>{t("Data")}</th>
          </tr>
        </thead>
        <tbody>
          {props.eventData.map((event) => {
            return (
              <tr key={event.count} onDoubleClick={() => props.jumpToTime(event.time)}>
                <td>{event.timeText}</td>
                <td>{event.symbol}</td>
                <td>{event.text}</td>
                <td>{event.data}</td>
              </tr>
            )
          })}
        </tbody>
      </Table>
      </div>
    </>
  )
}

export default Events
