/**
 * Contains the chart being displayed from the uploaded file
 * Appears only if a .dfb file is uploaded
 */
// Components
import Events from "./Events"
import Scale from "./Scale"

// CSS
import "../style/Chart.css"

// D3js
import * as d3 from "d3"

// React
// eslint-disable-next-line no-use-before-define
import React, { useEffect, useRef, useState } from "react"

// Object containing ECG event symbols with corresponding hex code
const eventSymbols = {
  0x01a: "ON",
  0x00d: "CONN",
  0x00f: "AN",
  0x004: "SHKADV",
  0x005: "NOSHK",
  0x006: "CH",
  0x011: "WTS",
  0x012: "SHK",
  0x007: "SHKDLV",
  0x013: "CPR",
  0x002: "~",
  0x003: "E~",
  0x00e: "INT",
  0x001: "SD",
  0x202: "OFF",
  0x1e: "WARN",
  0x1f: "ERR"
}

// ECG event test which corresponding symbols
const eventText = {
  ON: "Power On Event (for Flash Log)",
  CONN: "Start Wait for Pads to Be Applied",
  AN: "Start Analyzing Heart Rhythm",
  SHKADV: "Shock Advised",
  NOSHK: "No Shock Advised",
  CH: "Start Charging the Capacitor",
  WTS: "Start Wait for Shock Button + Shock Delivery",
  SHK: "Start Shock Delivery",
  SHKDLV: "Shock Delivered",
  CPR: "Start Pausing for CPR, Airways, ...",
  "~": "Start of Motion",
  "E~": "End of Motion",
  INT: "Start Signal Interferance Present",
  SD: "Shutdown Event Marker",
  OFF: "User Power Off Request",
  WARN: "Warning code",
  ERR: "Error code"
}

// Number of datapoints in a second.
const dataPerSec = 250

// Takes an epoch time and returns a JS date object time string.
function getDate (epochTime) {
  return new Date(epochTime * 1000).toTimeString()
}

function convertRemToPixels (rem) {
  return rem * parseFloat(getComputedStyle(document.documentElement).fontSize)
}

const Chart = (props) => {
  // Deleting chart before creating.
  d3.selectAll("svg").remove()
  d3.selectAll("g").remove()

  // References to DOM elements
  const topChart = useRef()
  const innerTopChart = useRef()
  const bottomChart = useRef()
  const innerBottomChart = useRef()
  const container = useRef()

  // States
  const [voltage, setVoltage] = useState(8)
  const [time, setTime] = useState(1)
  const [isInverted, setInverted] = useState(false)
  const [isGrids, setGrids] = useState(true)
  const [isTags, setTags] = useState(true)

  // Creating an array of the event data to be displayed in the event table and on the chart.
  const eventDataTop = []
  const eventDataBottom = []
  const eventData = []
  for (let i = 0; i < props.data.eventSymbols.length; i++) {
    if (
      props.data.eventTimes[i] >= props.data.eventTimes[0] &&
      props.data.eventSymbols[i] !== "" &&
      props.data.eventSymbols[i] in eventSymbols
    ) {
      if (
        props.data.eventTimes[i] >=
        props.data.times[props.data.times.length - dataPerSec * 6 * time] +
          props.data.startTime
      ) {
        eventDataBottom.push({
          symbol: eventSymbols[props.data.eventSymbols[i]],
          timeText: getDate(props.data.eventTimes[i]),
          time: props.data.eventTimes[i],
          text: eventText[eventSymbols[props.data.eventSymbols[i]]],
          count: i
        })
      } else if (
        props.data.eventTimes[i] <=
        props.data.times[dataPerSec * 6 * time] + props.data.startTime
      ) {
        eventDataTop.push({
          symbol: eventSymbols[props.data.eventSymbols[i]],
          timeText: getDate(props.data.eventTimes[i]),
          time: props.data.eventTimes[i],
          text: eventText[eventSymbols[props.data.eventSymbols[i]]],
          count: i
        })
      } else {
        eventDataTop.push({
          symbol: eventSymbols[props.data.eventSymbols[i]],
          timeText: getDate(props.data.eventTimes[i]),
          time: props.data.eventTimes[i],
          text: eventText[eventSymbols[props.data.eventSymbols[i]]],
          count: i
        })
        eventDataBottom.push({
          symbol: eventSymbols[props.data.eventSymbols[i]],
          timeText: getDate(props.data.eventTimes[i]),
          time: props.data.eventTimes[i],
          text: eventText[eventSymbols[props.data.eventSymbols[i]]],
          count: i
        })
      }
      eventData.push({
        symbol: eventSymbols[props.data.eventSymbols[i]],
        timeText: getDate(props.data.eventTimes[i]),
        time: props.data.eventTimes[i],
        text: eventText[eventSymbols[props.data.eventSymbols[i]]],
        count: i
      })
    }
  }

  // Display date and time for file
  const startDateAndTime = new Date(props.data.startTime * 1000)

  props.setTime(time)
  props.setVoltage(voltage)
  props.setIsTags(isTags)
  props.setIsGrids(isGrids)
  props.setInverted(isInverted)

  useEffect(() => {
    const data = []

    // Generating objects for d3 to use
    for (let i = 0; i < props.data.voltages.length; i++) {
      // If the Inver box is checked, data will be inverted
      let voltageValue
      if (isInverted) {
        voltageValue = props.data.voltages[i] * -1
      } else {
        voltageValue = props.data.voltages[i]
      }

      data.push({
        timeStamp: (props.data.times[i] + props.data.startTime) * 1000,
        value: voltageValue
      })
    }

    // Setting boundaries of the chart
    const height = 250
    const margin = { top: 0, right: 20, bottom: 30, left: 30 }
    const width = container.current.offsetWidth - convertRemToPixels(2)
    jumpConst = width / 2

    // const ceilSec = Math.ceil(data.length / dataPerSec)
    let ceilSec = Math.ceil(
      (data[data.length - 1].timeStamp - data[0].timeStamp) / 1000
    )

    let cutFrames = 2

    if (ceilSec <= time * 6) {
      ceilSec = time * 6
      cutFrames = 0
    }
    let innerWidth = width * ((ceilSec - cutFrames) / (time * 6))

    let topChartDomain = data[0].timeStamp + (ceilSec - cutFrames) * 1000
    let bottomChartDomain =
      data[0].timeStamp + time * 6 * 1000 + (ceilSec - cutFrames) * 1000

    const topChartData = []
    const bottomChartData = []

    for (let i = 0; i < data.length; i++) {
      if (data[i].timeStamp <= topChartDomain) {
        topChartData.push(data[i])
      }

      if (data[i].timeStamp >= data[0].timeStamp + time * 6 * 1000) {
        bottomChartData.push(data[i])
      }
    }

    if (ceilSec <= time * 6) {
      innerWidth = width
      topChartDomain = data[0].timeStamp + time * 6 * 1000
      bottomChartDomain = new Date(
        data[0].timeStamp + time * 6 * 1000 + time * 6 * 1000
      )
    }

    // X-axis scale for the top chart
    const topX = d3
      .scaleTime()
      .domain([new Date(data[0].timeStamp), new Date(topChartDomain)])
      .range([0, innerWidth])

    // X-axis scale for the bottom chart
    const bottomX = d3
      .scaleTime()
      .domain([
        new Date(data[0].timeStamp + time * 6 * 1000),
        new Date(bottomChartDomain)
      ])
      .range([0, innerWidth])

    // Y-axis scale
    const y = d3
      .scaleLinear()
      .domain([-1 * voltage, voltage])
      .range([height - margin.bottom, margin.bottom])

    // Time format for the ticks
    const timeFormat = d3.timeFormat("%H:%M:%S")

    let numTicks = (ceilSec - cutFrames) / time
    if ((ceilSec - cutFrames) / time <= 6) {
      numTicks = 6
    }

    // X-axis tick locations
    const bottomXAxis = (g) =>
      g.attr("transform", `translate(0, ${height - margin.bottom})`).call(
        d3
          .axisBottom(bottomX)
          .ticks(numTicks)
          .tickFormat(timeFormat)
      )

    // X-axis tick locations
    const topXAxis = (g) =>
      g.attr("transform", `translate(0, ${height - margin.bottom})`).call(
        d3
          .axisBottom(topX)
          .ticks(numTicks)
          .tickFormat(timeFormat)
      )

    // Y-axis tick locations
    const yAxis = (g) =>
      g
        .attr("transform", `translate(${margin.left},0)`)
        .call(d3.axisLeft(y).ticks(3))
        .call(
          d3
            .axisLeft(y)
            .tickValues([voltage * -1, 0, voltage])
            .tickFormat(function (d) {
              return d3.format(".2f")(d)
            })
        )
        .call((g) => g.select(".domain").remove())
        .call((g) =>
          g
            .select(".tick:last-of-type text")
            .clone()
            .attr("x", 3)
            .attr("text-anchor", "start")
            .attr("font-weight", "bold")
            .text(data.y)
        )

    const totalTime =
      props.data.times[props.data.times.length - 1] - props.data.times[0]

    // Takes a D3 chart and adds tags along the x axis for the bottom chart.
    const createTagsBottom = (g) => {
      let tagHeight = 0.75
      for (let i = 0; i < eventDataBottom.length; i++) {
        const relativeTime = eventDataBottom[i].time - topChartDomain
        if (i === 0) {
          tagHeight = 0.75
        } else if (eventDataBottom[i].time === eventDataBottom[i - 1].time) {
          tagHeight = 0.85
        } else {
          tagHeight = 0.75
        }
        g.append("text")
          .attr("y", height * tagHeight)
          .style("fill", "red")
          .attr("x", (relativeTime / totalTime) * innerWidth)
          .attr("text-anchor", "middle")
          .attr("class", "myLabel")
          .text(eventDataBottom[i].symbol)
      }
    }

    // Takes a D3 chart and adds tags along the x axis for the top chart.
    const createTagsTop = (g) => {
      let tagHeight = 0.75
      for (let i = 0; i < eventDataTop.length; i++) {
        const relativeTime =
          eventDataTop[i].time - (props.data.times[0] + props.data.startTime)
        if (i === 0) {
          tagHeight = 0.75
        } else if (eventDataTop[i].time === eventDataTop[i - 1].time) {
          tagHeight = 0.85
        } else {
          tagHeight = 0.75
        }
        g.append("text")
          .attr("y", height * tagHeight)
          .style("fill", "red")
          .attr("x", (relativeTime / totalTime) * innerWidth)
          .attr("text-anchor", "middle")
          .attr("class", "myLabel")
          .text(eventDataTop[i].symbol)
      }
    }

    const createGrids = (g) => {
      // Horizontal Lines
      const relativeHeight = height - margin.bottom - margin.bottom
      for (let i = 0; i < voltage * 2 + 1; i++) {
        g.append("line")
          .style("stroke", "rgb(200,200,200)")
          .style("stroke-width", 1)
          .attr("x1", 0)
          .attr("y1", relativeHeight * (i / (voltage * 2)) + margin.bottom)
          .attr("x2", innerWidth)
          .attr("y2", relativeHeight * (i / (voltage * 2)) + margin.bottom)
      }

      // Vertical Lines
      for (let i = 0; i < ceilSec - cutFrames; i++) {
        g.append("line")
          .style("stroke", "rgb(200,200,200)")
          .style("stroke-width", 1)
          .attr("x1", innerWidth * (i / (ceilSec - cutFrames)))
          .attr("y1", margin.bottom)
          .attr("x2", innerWidth * (i / (ceilSec - cutFrames)))
          .attr("y2", height - margin.bottom)
      }
    }

    const createEndLine = (g) => {
      const relativeEndTime = data[data.length - 1].timeStamp

      let xPosition =
        innerWidth *
        ((relativeEndTime - (data[0].timeStamp + time * 6 * 1000)) /
          (data[0].timeStamp +
            time * 6 * 1000 +
            (ceilSec - cutFrames) * 1000 -
            (data[0].timeStamp + time * 6 * 1000)))

      if (bottomChartData.length === 0) {
        xPosition =
          innerWidth *
          ((relativeEndTime - data[0].timeStamp) / (time * 6 * 1000))
      }

      g.append("line")
        .style("stroke", "rgb(200,0,0)")
        .style("stroke-width", 1)
        .attr("x1", xPosition)
        .attr("y1", margin.bottom)
        .attr("x2", xPosition)
        .attr("y2", height - margin.bottom)
    }

    // In order to create scrolling with the chart, a parent and body div are created to have the body div scroll within the parent div.
    const parent = d3.select(bottomChart.current)

    // Creating the attributes and styling for the parent div.
    parent
      .append("svg")
      .attr("width", width)
      .attr("height", height)
      .style("position", "absolute")
      .style("pointer-events", "none")
      .style("z-index", 1)
      .call((svg) => svg.append("g").call(yAxis))

    // Adding the body div to the parent div.
    const body = d3.select(innerBottomChart.current)

    // Adding styling and attributes to the body div along with the chart.
    body
      .append("svg")
      .attr("width", innerWidth)
      .attr("height", height)
      .style("display", "block")
      .call((svg) => svg.append("g").call(bottomXAxis))
      .call((svg) => {
        if (isGrids) svg.append("g").call(createGrids)
      })
      .call((svg) => {
        if (isTags && time <= 2) svg.append("g").call(createTagsBottom)
      })
      .call((svg) => {
        if (bottomChartData.length !== 0) svg.append("g").call(createEndLine)
      })
      .append("path")
      .datum(bottomChartData)
      .attr("fill", "none")
      .attr("stroke", "blue")
      .attr("stroke-width", 1)
      .attr(
        "d",
        d3
          .line()
          .x(function (d) {
            return bottomX(d.timeStamp)
          })
          .y(function (d) {
            return y(d.value)
          })
      )

    const scale = d3.scaleLinear().domain([0, 1000000000]).range([0, width])

    const gridlines = d3.axisTop().tickFormat("").tickSize(-height).scale(scale)

    body.append("g").attr("class", "grid").call(gridlines)

    // In order to create scrolling with the chart, a parent and body div are created to have the body div scroll within the parent div.
    const parentTop = d3.select(topChart.current)

    // Creating the attributes and styling for the parent div.
    parentTop
      .append("svg")
      .attr("width", width)
      .attr("height", height)
      .style("position", "absolute")
      .style("pointer-events", "none")
      .style("z-index", 1)
      .style("overflow", "hidden")
      .call((svg) => svg.append("g").call(yAxis))

    // Adding the body div to the parent div.
    const bodyTop = d3.select(innerTopChart.current)

    // Adding styling and attributes to the body div along with the chart.
    bodyTop
      .append("svg")
      .attr("width", innerWidth)
      .attr("height", height)
      .style("display", "block")
      .call((svg) => svg.append("g").call(topXAxis))
      .call((svg) => {
        if (isGrids) svg.append("g").call(createGrids)
      })
      .call((svg) => {
        if (isTags && time <= 2) svg.append("g").call(createTagsTop)
      })
      .call((svg) => {
        if (bottomChartData.length === 0) svg.append("g").call(createEndLine)
      })
      .append("path")
      .datum(topChartData)
      .attr("fill", "none")
      .attr("stroke", "blue")
      .attr("stroke-width", 1)
      .attr(
        "d",
        d3
          .line()
          .x(function (d) {
            return topX(d.timeStamp)
          })
          .y(function (d) {
            return y(d.value)
          })
      )
  }, [
    props.data,
    voltage,
    time,
    isInverted,
    isTags,
    isGrids,
    startDateAndTime
  ])

  let jumpConst = 700

  function jumpToTime (timeStamp) {
    innerBottomChart.current.scrollLeft =
      ((timeStamp - (props.data.times[0] + props.data.startTime)) /
        (props.data.times[props.data.times.length - 1] +
          props.data.startTime -
          (props.data.times[0] + props.data.startTime))) *
        innerBottomChart.current.scrollWidth -
      jumpConst
    innerTopChart.current.scrollLeft =
      ((timeStamp - (props.data.times[0] + props.data.startTime)) /
        (props.data.times[props.data.times.length - 1] +
          props.data.startTime -
          (props.data.times[0] + props.data.startTime))) *
        innerBottomChart.current.scrollWidth -
      jumpConst
  }

  let isSyncingBottomScroll = false
  let isSyncingTopScroll = false

  // Controls the synchronization of the bottom chart when the top chart is scrolled
  function topChartScroll () {
    if (!isSyncingBottomScroll) {
      isSyncingTopScroll = true
      innerBottomChart.current.scrollLeft = innerTopChart.current.scrollLeft
    }
    isSyncingBottomScroll = false
  }

  // Controls the synchronization of the top chart when the bottom chart is scrolled
  function bottomChartScroll () {
    if (!isSyncingTopScroll) {
      isSyncingBottomScroll = true
      innerTopChart.current.scrollLeft = innerBottomChart.current.scrollLeft
    }
    isSyncingTopScroll = false
  }

  return (
    <>
      <h4 style={{ margin: 0 }}>{props.fileName + " " + startDateAndTime}</h4>
      <div ref={container}>
        <div ref={topChart}></div>
        <div
          className="inner-div"
          onScroll={topChartScroll}
          ref={innerTopChart}
        ></div>
        <div ref={bottomChart}></div>
        <div
          className="inner-div"
          onScroll={bottomChartScroll}
          ref={innerBottomChart}
        ></div>
      </div>

      <Scale
        setVoltage={setVoltage}
        setTime={setTime}
        setInverted={setInverted}
        setTags={setTags}
        setGrids={setGrids}
      />

      <div id="chart-settings" style={{ height: "20%" }}>
        <Events eventData={eventData} jumpToTime={jumpToTime} />
      </div>
    </>
  )
}

export default Chart
