// This component is used to contain the header and body components.

// Components
import Chart from "./components/Chart"
import FileAlert from "./components/FileAlert"
import Navigation from "./components/Navigation"

// CSS
import "./App.css"

// React
// eslint-disable-next-line no-use-before-define
import React, { useState } from "react"

function App () {
  const [data, setData] = useState({ voltages: [0], times: [0], eventSymbols: [0], eventTimes: [0], startTime: 0, numEntries: 0 })
  const [isUploaded, setIsUploaded] = useState(false)
  const [showAlert, setShowAlert] = useState(false)
  const [alertMessage, setAlertMessage] = useState("")
  const [fileName, setFileName] = useState()
  const [date, setDate] = useState()
  const [isGrids, setIsGrids] = useState()
  const [isTags, setIsTags] = useState()
  const [voltage, setVoltage] = useState()
  const [time, setTime] = useState()
  const [isInverted, setInverted] = useState()

  return (
    <div className="container">
      <Navigation isGrids={isGrids} isTags={isTags} voltage={voltage} time={time} setData={setData} data={data} setIsUploaded={setIsUploaded} setShowAlert={setShowAlert} setAlertMessage={setAlertMessage} setFileName={setFileName} date={date} isInverted={isInverted} />
      <FileAlert show={showAlert} alertMessage={alertMessage} setShowAlert={setShowAlert} />
      { isUploaded && <Chart data={data} fileName={fileName} setDate={setDate} setIsGrids={setIsGrids} setIsTags={setIsTags} setVoltage={setVoltage} setInverted={setInverted} setTime={setTime} /> }
    </div>
  )
}

export default App
