/* eslint-disable camelcase */
/**
 * Contains the file that is uploaded
 * File is sent to the getECG.c file to sort
 * Sorted data is then passed to the Chart component for display
 */

// CSS
import "../style/Upload.css"

// React
import { useState, useEffect, useRef } from "react"

// react-i18next
import { useTranslation } from "react-i18next"

// C files
import createModule from "../getECG.mjs"

// react-bootstrap
import NavDropdown from "react-bootstrap/NavDropdown"

const Upload = (props) => {
  // i18next translations hook
  const { t } = useTranslation()

  let fileReader

  const [ProcessData, setProcessData] = useState()
  const [TimeData, setTimeData] = useState()
  const [Module, setModule] = useState()

  // reference hook for hidden input element
  const inputField = useRef()

  useEffect(() => {
    createModule().then((instance) => {
      setModule(instance)
      // need to use callback form (() => function) to ensure that `add` is set to the function
      setProcessData(() =>
        instance.cwrap("ProcessData", "number", [
          "number",
          "number",
          "number",
          "number",
          "string",
          "number"
        ])
      )
      setTimeData(() => instance.cwrap("getStartDateAndTime", "number", []))
    })
  }, [])
  if (!ProcessData || !TimeData) {
    return "Loading webassembly..."
  }

  function swap16 (val) {
    return (val << 8) | (val >> 8)
  }

  const callfunction = (file_data) => {
    const file_length = file_data.length
    const file_data_8Bit = new Uint8Array(file_data.buffer)

    const input_ptr = Module._malloc(file_length * 2)
    const outEcgValuePtr = Module._malloc(file_length * 4)
    const outEcgTimePtr = Module._malloc(file_length * 4)
    const outEventMarkerPtr = Module._malloc(file_length * 2)
    const outEventMarkerTimeStampPtr = Module._malloc(file_length * 4)

    Module.writeArrayToMemory(file_data_8Bit, input_ptr)
    const ecgEntries = Module._ProcessData(
      input_ptr,
      file_length,
      outEcgValuePtr,
      outEcgTimePtr,
      outEventMarkerPtr,
      outEventMarkerTimeStampPtr
    )

    // Get the start date and time of the ECG event
    const startTime = Module._getStartDateAndTime()

    // Get the ECG & Time.
    const js_ecg_data = new Float32Array(
      Module.HEAP8.buffer,
      outEcgValuePtr,
      ecgEntries
    )
    const js_time_data = new Float32Array(
      Module.HEAP8.buffer,
      outEcgTimePtr,
      ecgEntries
    )

    const js_event_marker_symbols = new Uint16Array(
      Module.HEAP8.buffer,
      outEventMarkerPtr,
      ecgEntries
    )

    const js_event_marker_time_stamps = new Uint32Array(
      Module.HEAP8.buffer,
      outEventMarkerTimeStampPtr,
      ecgEntries
    )

    props.setData({ voltages: js_ecg_data, times: js_time_data, eventSymbols: js_event_marker_symbols, eventTimes: js_event_marker_time_stamps, startTime: startTime, numEntries: ecgEntries })

    // Free all the pointers
    Module._free(input_ptr)
    Module._free(outEcgValuePtr)
    Module._free(outEcgTimePtr)
    Module._free(outEventMarkerPtr)
    Module._free(outEventMarkerTimeStampPtr)
  }

  const handleFileRead = (e) => {
    const file_data = new Uint16Array(fileReader.result)
    for (let i = 0; i < file_data.length; i++) {
      file_data[i] = swap16(file_data[i])
    }
    callfunction(file_data)
    props.setIsUploaded(true)
  }

  // When new file is selected
  function onFileChange (file) {
    // In case user doesn't select a file.
    if (file === undefined) {
      return
    }

    const extension = file.name.split(".").pop().toLowerCase() // file extension from input file

    // In case user doesn't select a file.
    if (extension === undefined) {
      return
    }

    // Checking if file extension is .DFB
    if (extension !== "dfb") {
      props.setIsUploaded(false)
      props.setShowAlert(true)
      props.setAlertMessage("Invalid file extension type. Must be .DFB")
      return
    }

    props.setFileName(file.name)

    fileReader = new FileReader()
    fileReader.onloadend = handleFileRead
    fileReader.readAsArrayBuffer(file)
  }

  // Handles the click of the NavDropdown.Item and clicks the input file button that is hidden.
  function handleClick () {
    inputField.current.click()
  }

  // Clears the forms before checking for onChange event.
  function handleFormClear (e) {
    e.target.value = ""
  }

  return (
    <>

      {/* The file input is being called as a ref from the NavDropdown.Item component, creating a custom file input from a NavDropdown.Item */}
      <NavDropdown.Item onClick={() => handleClick()} type="file">{t("Open")}</NavDropdown.Item>
      <input accept=".dfb" ref={inputField}
        type='file'
        onClick={(e) => {
          handleFormClear(e)
        }}

        onChange={(e) => {
          onFileChange(e.target.files[0])
        }} hidden />
    </>
  )
}

export default Upload
