/**
 * Contains the unimplemented download button for downloading the
 * current displayed chart in a pdf format.
 */

// Canvg
import Canvg, { presets } from "canvg"

// D3js
import * as d3 from "d3"

// jsPDF
// import { scaleOrdinal } from "d3"
import jsPDF from "jspdf"
import "jspdf-autotable"

// react-bootstrap
import { NavDropdown } from "react-bootstrap"

// react-i18next
import { useTranslation } from "react-i18next"

import { useState, useContext } from "react"

import { ContactContext } from "../context/ContactContext"

const eventSymbols = {
  0x01a: "ON",
  0x00d: "CONN",
  0x00f: "AN",
  0x004: "SHKADV",
  0x005: "NOSHK",
  0x006: "CH",
  0x011: "WTS",
  0x012: "SHK",
  0x007: "SHKDLV",
  0x013: "CPR",
  0x002: "~",
  0x003: "E~",
  0x00e: "INT",
  0x001: "SD",
  0x202: "OFF",
  0x1e: "WARN",
  0x1f: "ERR"
}

const eventText = {
  ON: "Power On Event (for Flash Log)",
  CONN: "Start Wait for Pads to Be Applied",
  AN: "Start Analyzing Heart Rhythm",
  SHKADV: "Shock Advised",
  NOSHK: "No Shock Advised",
  CH: "Start Charging the Capacitor",
  WTS: "Start Wait for Shock Button + Shock Delivery",
  SHK: "Start Shock Delivery",
  SHKDLV: "Shock Delivered",
  CPR: "Start Pausing for CPR, Airways, ...",
  "~": "Start of Motion",
  "E~": "End of Motion",
  INT: "Start Signal Interferance Present",
  SD: "Shutdown Event Marker",
  OFF: "User Power Off Request",
  WARN: "Warning code",
  ERR: "Error code"
}

const Download = (props) => {
  // Takes an epoch time and returns a JS date object time string.
  function getDate (epochTime) {
    return new Date(epochTime * 1000).toTimeString()
  }

  const { contactInfo } = useContext(ContactContext)

  const { t } = useTranslation()

  const [eventDuration] = useState(props.data.times.length / 250)

  function createHeaderAndFooter (doc, page, totalPages) {
    doc.text(t("Event Data") + ":", 9, 14)
    doc.text(new Date(props.data.startTime * 1000).toDateString(), 49, 14)

    doc.text(t("Patient") + ":", 180, 14)
    doc.text(
      contactInfo.lastName +
        ", " +
        contactInfo.firstName +
        " " +
        contactInfo.middleName,
      200,
      14
    )

    doc.text(t("Event Duration") + ":", 9, 18)
    doc.text(String(eventDuration) + " ECG seconds", 49, 18)

    doc.text(t("MRN") + ":", 180, 18)
    doc.text(contactInfo.mrn, 200, 18)

    doc.text(t("File name") + ":", 9, 22)
    doc.text(props.fileName, 49, 22)

    doc.text(t("SSN") + ":", 180, 22)
    doc.text(contactInfo.ssn, 200, 22)

    doc.line(10, 25, 270, 25)

    doc.line(10, 190, 270, 190)
    doc.text(t("Facility Name") + ":", 10, 195)
    doc.text(contactInfo.facility, 50, 195)

    doc.text(t("Printed") + ":", 180, 195)
    doc.text(new Date(Date.now()).toTimeString(), 200, 195)

    doc.text(t("Page") + " " + page + " " + t("of") + totalPages, 200, 200)
  }

  // Creating an array of the event data to be displayed in the event table and on the chart.
  const eventData = []
  for (let i = 0; i < props.data.eventSymbols.length; i++) {
    if (
      props.data.eventTimes[i] >= props.data.eventTimes[0] &&
      props.data.eventSymbols[i] !== "" &&
      props.data.eventSymbols[i] in eventSymbols
    ) {
      eventData.push({
        Symbol: eventSymbols[props.data.eventSymbols[i]],
        Time: getDate(props.data.eventTimes[i]),
        timeNumber: props.data.eventTimes[i] * 1000,
        Text: eventText[eventSymbols[props.data.eventSymbols[i]]],
        Data: " "
      })
    }
  }

  async function downloadPDF () {
    // eslint-disable-next-line new-cap
    const doc = new jsPDF({
      orientation: "l",
      format: "letter"
    })

    const chartPages = Math.ceil(eventDuration / (6 * props.time) / 4)

    const totalPages = chartPages + 2

    doc.setFontSize(10)
    doc.setFont("Times")

    // Event
    createHeaderAndFooter(doc, 1, totalPages)

    doc.text(t("Location") + ":", 30, 40)
    doc.text(t("Responder") + ":", 30, 45)
    doc.text(t("Original File") + ":", 30, 50)
    doc.text(props.fileName, 60, 50)
    doc.text(t("Shocks") + ":", 30, 60)
    doc.text(t("Patient Data") + ":", 30, 70)
    doc.text(t("Name") + ":", 40, 80)
    doc.text(
      contactInfo.lastName +
        ", " +
        contactInfo.firstName +
        " " +
        contactInfo.middleName,
      60,
      80
    )
    doc.text(t("Address") + ":", 40, 90)
    doc.text(contactInfo.address, 60, 90)
    doc.text(t("Telephone") + ":", 40, 110)
    doc.text(contactInfo.phone, 60, 110)
    doc.text(t("Gender") + ":", 40, 120)
    doc.text(t("Age") + ":", 40, 125)
    doc.text(t("DOB") + ":", 40, 130)

    let x
    let y

    let yAxis
    // X-axis tick locations
    const xAxis = (g) =>
      g
        .attr("transform", `translate(0, ${height - 30})`)
        .call(d3.axisBottom(x).ticks(6).tickFormat(timeFormat))

    // Time format for the ticks
    const timeFormat = d3.timeFormat("%H:%M:%S")

    const height = 40 * 5
    const margin = { top: 20, right: 0, bottom: 30, left: 30 }
    const width = 250 * 5

    const yCoords = [25, 65, 105, 145]

    const createGrids = (g) => {
      // Horizontal Lines
      const relativeHeight = height - margin.bottom - margin.bottom
      for (let i = 0; i < props.voltage * 2 + 1; i++) {
        g.append("line")
          .style("stroke", "gray")
          .style("stroke-width", 1)
          .attr("x1", 0)
          .attr(
            "y1",
            relativeHeight * (i / (props.voltage * 2)) + margin.bottom
          )
          .attr("x2", width)
          .attr(
            "y2",
            relativeHeight * (i / (props.voltage * 2)) + margin.bottom
          )
      }

      // Vertical Lines
      const relativeWidth = width - margin.left
      for (let i = 0; i <= props.time * 6; i++) {
        g.append("line")
          .style("stroke", "gray")
          .style("stroke-width", 1)
          .attr("x1", relativeWidth * (i / (props.time * 6)))
          .attr("y1", margin.bottom)
          .attr("x2", relativeWidth * (i / (props.time * 6)))
          .attr("y2", height - margin.bottom)
      }
    }

    let startDomain = (props.data.times[0] + props.data.startTime) * 1000

    // Charts
    for (let i = 2; i < totalPages; i++) {
      doc.addPage("l", "letter")

      doc.setFontSize(8)
      doc.text(t("Scale: mVolts / time"), 13, 30)

      doc.setFontSize(12)
      doc.text(t("NOT FOR DIAGNOSTIC PURPOSES"), 100, 30)

      doc.setFontSize(10)

      createHeaderAndFooter(doc, i, totalPages)

      for (let j = 0; j < 4; j++) {
        const data = []
        for (let k = 0; k < 250 * 6 * props.time; k++) {
          if (
            k + 250 * j * 6 * props.time + 250 * (i - 2) * 6 * 4 * props.time >=
            props.data.times.length
          ) {
            break
          }
          if (props.isInverted) {
            data.push({
              timeStamp:
                (props.data.times[
                  k +
                    250 * j * 6 * props.time +
                    250 * (i - 2) * 6 * 4 * props.time
                ] +
                  props.data.startTime) *
                1000,
              voltage:
                -1 *
                props.data.voltages[
                  k +
                    250 * j * 6 * props.time +
                    250 * (i - 2) * 6 * 4 * props.time
                ]
            })
          } else {
            data.push({
              timeStamp:
                (props.data.times[
                  k +
                    250 * j * 6 * props.time +
                    250 * (i - 2) * 6 * 4 * props.time
                ] +
                  props.data.startTime) *
                1000,
              voltage:
                props.data.voltages[
                  k +
                    250 * j * 6 * props.time +
                    250 * (i - 2) * 6 * 4 * props.time
                ]
            })
          }
        }

        const curEventData = []

        const createTags = (g) => {
          let tagHeight = 0.75
          for (let i = 0; i < eventData.length; i++) {
            if (
              eventData[i].timeNumber >= startDomain &&
              eventData[i].timeNumber <= startDomain + props.time * 6000
            ) {
              curEventData.push(eventData[i])
            }
          }

          for (let i = 0; i < curEventData.length; i++) {
            const relativeTime = curEventData[i].timeNumber
            if (i === 0) {
              tagHeight = 0.75
            } else if (
              curEventData[i].timeNumber === curEventData[i - 1].timeNumber
            ) {
              tagHeight = 0.85
            } else {
              tagHeight = 0.75
            }
            g.append("text")
              .attr("y", tagHeight * height)
              .style("fill", "red")
              .attr(
                "x",
                ((relativeTime - startDomain) / (props.time * 6000)) * width
              )
              .attr("text-anchor", "middle")
              .attr("class", "myLabel")
              .text(curEventData[i].Symbol)
          }
        }

        x = d3
          .scaleTime()
          .domain([
            new Date(startDomain),
            new Date(startDomain + props.time * 6000)
          ])
          .range([margin.left, width])

        // Y-axis scale
        y = d3
          .scaleLinear()
          .domain([-1 * props.voltage, props.voltage])
          .range([height - margin.bottom, margin.bottom])

        let voltageTicks = [-1 * props.voltage, 0, props.voltage]

        if (props.isInverted) {
          voltageTicks = [props.voltage, 0, props.voltage * -1]
        }

        // Y-axis tick locations
        yAxis = (g) =>
          g
            .attr("transform", `translate(${margin.left},0)`)
            .call(d3.axisLeft(y).ticks(3))
            .call(
              d3
                .axisLeft(y)
                .tickValues(voltageTicks)
                .tickFormat(function (d) {
                  return d3.format(".2f")(d)
                })
            )
            .call((svg) => svg.append("g").call(createGrids))
            .call((g) => g.select(".domain").remove())
            .call((g) =>
              g
                .select(".tick:last-of-type text")
                .clone()
                .attr("x", 3)
                .attr("text-anchor", "start")
                .attr("font-weight", "bold")
                .text(data.y)
            )

        const svg = d3.create("svg")

        // Adding styling and attributes to the body div along with the chart.
        svg
          .append("g")
          .call((svg) => svg.append("g").call(xAxis))
          .call((svg) => svg.append("g").call(yAxis))
          .call((svg) => {
            if (props.isTags && props.time < 5) svg.append("g").call(createTags)
          })
          .append("path")
          .datum(data)
          .attr("fill", "none")
          .attr("stroke", "blue")
          .attr("stroke-width", 1)
          .attr(
            "d",
            d3
              .line()
              .x(function (d) {
                return x(d.timeStamp)
              })
              .y(function (d) {
                return y(d.voltage)
              })
          )

        const canvas = new OffscreenCanvas(width, height)
        const ctx = canvas.getContext("2d")

        const svgElementSrc = new XMLSerializer().serializeToString(svg.node())

        const v = await Canvg.from(ctx, svgElementSrc, presets.offscreen())

        // Render only first frame, ignoring animations and mouse.
        await v.render()

        const blob = await canvas.convertToBlob()
        const pngUrl = URL.createObjectURL(blob)

        doc.addImage(pngUrl, "PNG", 10, yCoords[j], 250, 40, "", "NONE")

        startDomain += 6000 * props.time
      }
    }

    // Event Table
    doc.addPage("l", "letter")
    createHeaderAndFooter(doc, totalPages, totalPages)

    doc.getFontSize(12)
    doc.text(t("EVENT TAGS"), 100, 30)

    function createHeaders (keys) {
      const result = []
      for (let i = 0; i < keys.length; i += 1) {
        result.push({
          id: keys[i],
          name: keys[i],
          prompt: keys[i],
          width: 65,
          align: "center",
          padding: 0
        })
      }
      return result
    }

    const headers = createHeaders(["Time", "Symbol", "Text", "Data"])

    doc.table(35, 35, eventData, headers, { autoSize: true, fontSize: 10 })

    doc.save("ecg-chart")
  }

  return (
    <NavDropdown.Item
      onClick={() => {
        downloadPDF()
      }}
    >
      {t("Save as PDF")}
    </NavDropdown.Item>
  )
}

export default Download
