/**
 * Contains unimplimented alert that should pop up when a file of
 * an incorrect format (not .dfb) is uploaded
 */

// react-bootstrap
import { Col, Form } from "react-bootstrap"

// react-i18next
import { useTranslation } from "react-i18next"

const CheckboxSettings = (props) => {
  // i18next translation hook
  const { t } = useTranslation()

  return (
    <Col>
      <Form>
        <h5>{t(props.title)}</h5>
        <Form.Check
          inline
          label={t(props.labels[0])}
          name="group1"
          type={"checkbox"}
          onClick={(e) => props.setSettings[0](e.target.checked)}
        />
        <Form.Check
          inline
          label={t(props.labels[1])}
          name="group1"
          type={"checkbox"}
          onClick={(e) => props.setSettings[1](e.target.checked)}
          defaultChecked
        />
        <Form.Check
          inline
          label={t(props.labels[2])}
          type={"checkbox"}
          onClick={(e) => props.setSettings[2](e.target.checked)}
          defaultChecked
        />
      </Form>
    </Col>
  )
}

export default CheckboxSettings
