/**
 * Contains unimplimented alert that should pop up when a file of
 * an incorrect format (not .dfb) is uploaded
 */

// react-bootstrap
import { Container, Row } from "react-bootstrap"

import RadioSettings from "./RadioSettings"
import CheckboxSettings from "./CheckboxSettings"

const Scale = (props) => {
  return (
        <Container style={{ minHeight: "0%", marginTop: "0", marginBottom: "0" }}>
            <Row>
                <RadioSettings title={"Scale"} labels={["1v", "2v", "5v", "8v"]} values={[1, 2, 5, 8]} setSettings={props.setVoltage} defaultChecked={[false, false, false, true]}/>
                <RadioSettings title={"Window"} labels={["6s", "12s", "30s", "60s"]} values={[1, 2, 5, 10]} setSettings={props.setTime} defaultChecked={[true, false, false, false]}/>
                <CheckboxSettings title={"View"} labels={["Invert", "Tags", "Grids"]} setSettings={[props.setInverted, props.setTags, props.setGrids]} />
            </Row>
        </Container>
  )
}

export default Scale
