/**
 * Provides the drop down menu with the list of
 * languages the web page can be translated into.
 */
// CSS
import "../style/DropDown.css"

// react-bootstrap
import { Form } from "react-bootstrap"

// react-i18next
import { useTranslation } from "react-i18next"

const DropDown = () => {
  // translation hook
  const { i18n, t } = useTranslation()

  // changs language
  const changeLanguage = (language) => {
    i18n.changeLanguage(language)
  }

  return (
    <div
      id="dropdown-container"
      onChange={() =>
        changeLanguage(document.getElementById("languages").value)
      }
    >
      <Form.Group style={{ border: "0" }}>
        <Form.Control
          as="select"
          id="languages"
          defaultValue="Choose..."
          style={{ border: "0" }}
        >
          <option value="en">{t("English")}</option>
          <option value="it">{t("Italian")}</option>
          <option value="ca">{t("Chinese")}</option>
        </Form.Control>
      </Form.Group>
    </div>
  )
}

export default DropDown
