/**
 * Contains the popup to fill in the contact information
 */

// Components
import Col from "react-bootstrap/Col"

// CSS
import "../style/Popup.css"

// React
// eslint-disable-next-line no-use-before-define
import React, { useState, useEffect, useContext } from "react"

// react-i18next
import { useTranslation } from "react-i18next"

// react-bootstrap
import { Button, Form, Modal, NavDropdown } from "react-bootstrap"

import { ContactContext } from "../context/ContactContext"

const Contact = (props) => {
  // JSONs for dropdowns
  const states = require("../locations/states.json")
  const countries = require("../locations/countries.json")

  const { contactInfo, setContactInfo } = useContext(ContactContext)

  // React states
  const [modalShow, setModalShow] = useState(false)
  const [facility, setFacility] = useState("")
  const [title, setTitle] = useState("")
  const [salutation, setSalutation] = useState("")
  const [firstName, setFirstName] = useState("")
  const [middleName, setMiddleName] = useState("")
  const [lastName, setLastName] = useState("")
  const [address, setAddress] = useState("")
  const [city, setCity] = useState("")
  const [state, setState] = useState("")
  const [zipcode, setZipcode] = useState("")
  const [country, setCountry] = useState("")
  const [phone, setPhone] = useState("")
  const [extension, setExtension] = useState("")
  const [email, setEmail] = useState("")
  const [mrn] = useState("")
  const [ssn] = useState("")

  function handleSubmit () {
    localStorage.setItem(
      props.fileName,
      JSON.stringify({
        facility: facility,
        title: title,
        salutation: salutation,
        firstName: firstName,
        middleName: middleName,
        lastName: lastName,
        address: address,
        city: city,
        state: state,
        zipcode: zipcode,
        country: country,
        phone: phone,
        extension: extension,
        email: email,
        mrn: mrn,
        ssn: ssn
      })
    )

    setContactInfo({
      facility: facility,
      title: title,
      salutation: salutation,
      firstName: firstName,
      middleName: middleName,
      lastName: lastName,
      address: address,
      city: city,
      state: state,
      zipcode: zipcode,
      country: country,
      phone: phone,
      extension: extension,
      email: email,
      mrn: mrn,
      ssn: ssn
    })

    setModalShow(false)
  }

  // i18n translations hook
  const { t } = useTranslation()

  useEffect(() => {
    const localJSON = JSON.parse(localStorage.getItem(props.fileName))
    console.log(localJSON)
    if (localStorage.getItem(props.fileName)) {
      setContactInfo(localJSON)
      setFacility(localJSON.facility)
      setTitle(localJSON.title)
      setSalutation(localJSON.salutation)
      setFirstName(localJSON.firstName)
      setMiddleName(localJSON.middleName)
      setLastName(localJSON.lastName)
      setAddress(localJSON.address)
      setCity(localJSON.city)
      setState(localJSON.state)
      setZipcode(localJSON.zipcode)
      setCountry(localJSON.country)
      setPhone(localJSON.phone)
      setExtension(localJSON.extension)
      setEmail(localJSON.email)
    }
  }, [setContactInfo, props.fileName])

  return (
    <>
      { props.isUploaded
        ? <NavDropdown.Item onClick={() => setModalShow(true)}>
          {t("Contact Information")}
        </NavDropdown.Item>
        : <NavDropdown.Item disabled onClick={() => setModalShow(true)}>
          {t("Contact Information")}
        </NavDropdown.Item> }
      <Modal
        size="lg"
        show={modalShow}
        onHide={() => setModalShow(false)}
        aria-labelledby="contact-information-modal"
      >
        <Modal.Header closeButton>
          <Modal.Title id="contact-information-modal">
            {t("Contact Information")}
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Form.Group>
              <Form.Label>{t("Facility Name")}</Form.Label>
              <Form.Control
                type="text"
                placeholder=""
                onChange={(e) => {
                  setFacility(e.target.value)
                }}
                defaultValue={contactInfo.facility}
              />
            </Form.Group>

            <Form.Group>
              <Form.Label>{t("Title")}</Form.Label>
              <Form.Control
                type="text"
                placeholder=""
                onChange={(e) => {
                  setTitle(e.target.value)
                }}
                defaultValue={contactInfo.title}
              />
            </Form.Group>

            <Form.Group>
              <Form.Label>{t("Salutation")}</Form.Label>
              <Form.Control
                as="select"
                onChange={(e) => {
                  setSalutation(e.target.value)
                }}
                defaultValue={contactInfo.salutation}
              >
                <option value="mr">{t("Mr")}</option>
                <option value="mrs">{t("Mrs")}</option>
                <option value="ms">{t("Ms")}</option>
                <option value="dr">{t("Dr")}</option>
              </Form.Control>
            </Form.Group>

            <Form.Row>
              <Col xs={4}>
                <Form.Group>
                  <Form.Label>{t("First Name")}</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder=""
                    onChange={(e) => {
                      setFirstName(e.target.value)
                    }}
                    defaultValue={contactInfo.firstName}
                  />
                </Form.Group>
              </Col>

              <Col xs={1}>
                <Form.Group>
                  <Form.Label>{t("M. ")}</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder=""
                    onChange={(e) => {
                      setMiddleName(e.target.value)
                    }}
                    defaultValue={contactInfo.middleName}
                  />
                </Form.Group>
              </Col>

              <Col xs={4}>
                <Form.Group>
                  <Form.Label>{t("Last Name")}</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder=""
                    onChange={(e) => {
                      setLastName(e.target.value)
                    }}
                    defaultValue={contactInfo.lastName}
                  />
                </Form.Group>
              </Col>
            </Form.Row>

            <Form.Group>
              <Form.Label>{t("Address")}</Form.Label>
              <Form.Control
                placeholder=""
                onChange={(e) => {
                  setAddress(e.target.value)
                }}
                defaultValue={contactInfo.address}
              />
            </Form.Group>

            <Form.Row>
              <Col>
                <Form.Group>
                  <Form.Label>{t("City")}</Form.Label>
                  <Form.Control
                    onChange={(e) => {
                      setCity(e.target.value)
                    }}
                    defaultValue={contactInfo.city}
                  />
                </Form.Group>
              </Col>

              <Col>
                <Form.Group controlId="formGridState">
                  <Form.Label>{t("State")}</Form.Label>
                  <Form.Control
                    as="select"
                    donChange={(e) => {
                      setState(e.target.value)
                    }}
                    defaultValue={contactInfo.state}
                  >
                    <option>{t("Choose")}...</option>
                    {states.map((state) => {
                      return (
                        <option key={state.name} value={state.name}>
                          {state.name}
                        </option>
                      )
                    })}
                  </Form.Control>
                </Form.Group>
              </Col>

              <Col>
                <Form.Group controlId="formGridZip">
                  <Form.Label>{t("Zipcode")}</Form.Label>
                  <Form.Control
                    onChange={(e) => {
                      setZipcode(e.target.value)
                    }}
                    defaultValue={contactInfo.zipcode}
                  />
                </Form.Group>
              </Col>
            </Form.Row>

            <Form.Group controlId="formGridCountry">
              <Form.Label>{t("Country")}</Form.Label>
              <Form.Control
                as="select"
                onChange={(e) => {
                  setCountry(e.target.value)
                }}
                defaultValue={contactInfo.country}
              >
                <option>{t("Choose")}...</option>
                {countries.map((countries) => {
                  return (
                    <option key={countries.name} value={countries.name}>
                      {countries.name}
                    </option>
                  )
                })}
              </Form.Control>
            </Form.Group>

            <Form.Group controlId="formGridPhone">
              <Form.Label>{t("Phone")}</Form.Label>
              <Form.Control
                placeholder=""
                onChange={(e) => {
                  setPhone(e.target.value)
                }}
                defaultValue={contactInfo.phone}
              />
            </Form.Group>

            <Form.Group controlId="formGridExtension">
              <Form.Label>{t("Extension")}</Form.Label>
              <Form.Control
                placeholder=""
                onChange={(e) => {
                  setExtension(e.target.value)
                }}
                defaultValue={contactInfo.extension}
              />
            </Form.Group>

            <Form.Group controlId="formGridEmail">
              <Form.Label>{t("Email")}</Form.Label>
              <Form.Control
                placeholder=""
                onChange={(e) => {
                  setEmail(e.target.value)
                }}
                defaultValue={contactInfo.email}
              />
            </Form.Group>

            {props.children}
          </Form>
        </Modal.Body>

        <Modal.Footer>
          <Button variant="secondary" onClick={() => setModalShow(false)}>
            {t("Cancel")}
          </Button>
          <Button
            variant="primary"
            onClick={() => {
              handleSubmit()
            }}
          >
            {t("Submit")}
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  )
}

export default Contact
