/**
 * Contains unimplimented alert that should pop up when a file of
 * an incorrect format (not .dfb) is uploaded
 */

// react-bootstrap
import { Col, Form } from "react-bootstrap"

// react-i18next
import { useTranslation } from "react-i18next"

const RadioSettings = (props) => {
  // i18next translation hook
  const { t } = useTranslation()

  return (
    <Col>
      <Form onChange={(e) => props.setSettings(e.target.value)}>
        <h5>{t(props.title)}</h5>
        <Form.Check
          inline
          label={props.labels[0]}
          value={props.values[0]}
          name="group1"
          type={"radio"}
          id={`inline-${"radio"}-1`}
          defaultChecked={props.defaultChecked[0]}
        />

        <Form.Check
          inline
          label={props.labels[1]}
          value={props.values[1]}
          name="group1"
          type={"radio"}
          id={`inline-${"radio"}-1`}
          defaultChecked={props.defaultChecked[1]}
        />

        <Form.Check
          inline
          label={props.labels[2]}
          value={props.values[2]}
          name="group1"
          type={"radio"}
          id={`inline-${"radio"}-1`}
          defaultChecked={props.defaultChecked[2]}
        />

        <Form.Check
          inline
          label={props.labels[3]}
          value={props.values[3]}
          name="group1"
          type={"radio"}
          id={`inline-${"radio"}-1`}
          defaultChecked={props.defaultChecked[3]}
        />
      </Form>
    </Col>
  )
}

export default RadioSettings
