/**
 * Consists of the top strip of the web page which displays 'ECG Viewer'
 * and the drop down menu for translations.
 */
// Components
import Contact from "./Contact"
import Download from "./Download"
import Translations from "./Translations"
import Upload from "./Upload"

// CSS files
import "../style/Navigation.css"

// react imports
// eslint-disable-next-line no-use-before-define
import React, { useState, useEffect } from "react"

// react-bootstrap
import { Container, Form, Nav, Navbar, NavDropdown } from "react-bootstrap"

// react-i18next
import { useTranslation } from "react-i18next"

import { ContactProvider } from "../context/ContactContext"

const Navigation = (props) => {
  // i18next translations hook
  const { t } = useTranslation()

  // React state hooks
  const [isUploaded, setIsUploaded] = useState(false)
  const [fileName, setFileName] = useState()

  useEffect(() => {
    props.setIsUploaded(isUploaded)
    props.setFileName(fileName)
  })

  // Changes isUploaded to false, removing all components within Chart
  function handleCloseFile () {
    setIsUploaded(false)
  }

  return (
    <ContactProvider>
      <Navbar bg="light" expand="lg" fixed="top">
        <Container fluid id="nav-container">
          <Navbar.Brand>{t("ECG Viewer")}</Navbar.Brand>

          <NavDropdown title={t("File")} id="collasible-nav-dropdown">
            <Upload
              setIsUploaded={setIsUploaded}
              setFileName={setFileName}
              setData={props.setData}
              setShowAlert={props.setShowAlert}
              setAlertMessage={props.setAlertMessage}
            />
            {isUploaded && (
              <NavDropdown.Item onClick={() => handleCloseFile()}>
                {t("Close")}
              </NavDropdown.Item>
            )}
            {isUploaded && (
              <Download
                fileName={fileName}
                data={props.data}
                voltage={props.voltage}
                time={props.time}
                isGrids={props.isGrids}
                isTags={props.isTags}
                isInverted={props.isInverted}
              />
            )}
          </NavDropdown>

          <NavDropdown title={t("Settings")}>
            <Contact
              fileName={fileName}
              isUploaded={isUploaded}
              data={props.data}
            />
          </NavDropdown>

          <NavDropdown title={t("About")}>
            <NavDropdown.Item>{t("About")}</NavDropdown.Item>
          </NavDropdown>

          <Navbar.Collapse id="basic-navbar-nav justify-content-end">
            <Nav className="mr-auto"></Nav>

            <Form inline>
              <Translations className="d-flex justify-content-end" />
            </Form>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </ContactProvider>
  )
}

export default Navigation
