/**
 * Contains unimplimented alert that should pop up when a file of
 * an incorrect format (not .dfb) is uploaded
 */

// react-bootstrap
import { Alert } from "react-bootstrap"

const FileAlert = (props) => {
  if (props.show) {
    return (
      <Alert variant="danger" onClose={() => props.setShowAlert(false)} dismissible>
        <Alert.Heading>Oh snap! You got an error!</Alert.Heading>
        <p>
          {props.alertMessage}
        </p>
      </Alert>
    )
  }
  return []
}

export default FileAlert
